<template>
  <div>
    <b-navbar class="header-color" style="padding: 1% 1% 0 1%">
      <div aria-controls="sidebar" aria-expanded=""
           @click="show=true" class="text-light" id="category-button">
      <span class="header-color">
        <b-avatar icon="list" size="md" variant="" style="position: relative; top: -2px;"></b-avatar>
        <span style="font-size: 1.5em">Azraar</span>
      </span>
      </div>
      <div class="ml-auto">
        <b-navbar-nav style="float: left">
          <b-nav-item :to="{name: 'CartDetails'}">
            <div id="cartIcon" style="text-align: center;">
              <b-avatar v-if="cartProductCount !== '0'"
                        :badge="cartProductCount" badge-offset="-0.1em" badge-top
                        icon="handbag" class="header-color"
              ></b-avatar>
              <b-avatar v-else icon="handbag" class="header-color"></b-avatar><br>
            </div>
          </b-nav-item>
        </b-navbar-nav>
        <b-navbar-nav>
          <b-nav-item @click="$bvModal.show('login-modal')" v-if="!authToken">
            <b-avatar icon="box-arrow-in-right" size="" class="header-color" id="login-before"></b-avatar>
          </b-nav-item>
          <b-nav-item @click=handleLogOut v-else>
            <b-avatar icon="power" size="" class="header-color" id="logoutIcon"></b-avatar>
          </b-nav-item>
        </b-navbar-nav>
      </div>
      <b-sidebar id="sidebar" v-model="show" backdrop>
        <template v-slot:title>
          <span class="">Menu</span>
        </template>
        <div class="px-3 py-2">
          <b-list-group-item style="padding-left: 3%;">
            <show-category></show-category>
          </b-list-group-item>
          <b-list-group-item style="padding-left: 3%;">
            <show-brands></show-brands>
          </b-list-group-item>
          <div v-if="authToken">
            <b-list-group-item :to="{name: 'UserProfile'}" style="padding-left: 3%; color:black">
              <b-avatar icon="person" variant="" size=""></b-avatar>
              {{userNameToShow(user.name,20)}}
            </b-list-group-item>
            <b-list-group-item :to="{name: 'OrderShower'}" style="padding-left: 3%; color:black">
              <b-avatar icon="truck" variant="" size=""></b-avatar>
              Order history
            </b-list-group-item>
          </div>
        </div>
      </b-sidebar>
    </b-navbar>
    <b-navbar class="header-color" style="padding: 1%; position: relative; top: -1px">
      <div class="mx-auto">
        <search-bar :id="2" :width="width"></search-bar>
      </div>
    </b-navbar>
    <transition name="fade">
      <lower-header v-if="scrollPosition < 300" style="position: relative; top: -2px;"></lower-header>
    </transition>
  </div>
</template>

<script>
import ShowCategory from '@/components/ShowCategory'
import ShowBrands from '@/components/ShowBrands'
import { mapGetters } from 'vuex'
import userNameShower from '@/mixins/userNameShower'
import logoutHandler from '@/mixins/logoutHandler'
import SearchBar from '@/components/SearchBar'
import LowerHeader from '@/components/LowerHeader'

export default {
  name: 'MobileSideMenu',
  components: { LowerHeader, SearchBar, ShowCategory, ShowBrands },
  props: [
    'width'
  ],
  mixins: [
    userNameShower,
    logoutHandler
  ],
  computed: {
    ...mapGetters([
      'user',
      'authToken',
      'cartProductCount'
    ])
  },
  data () {
    return {
      show: false,
      scrollPosition: null
    }
  },
  methods: {
    updateScroll () {
      this.scrollPosition = window.scrollY
    }
  },
  mounted () {
    window.addEventListener('scroll', this.updateScroll)
  },
  destroy () {
    window.removeEventListener('scroll', this.updateScroll)
  }
}
</script>

<style scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>
