import { mapActions } from 'vuex'

export default {
  methods: {
    ...mapActions([
      'logOut'
    ]),
    handleLogOut () {
      this.logOut()
      const routeParent = this.$route.matched[0].name
      if (routeParent === 'User') {
        this.$router.push({ name: 'Home' })
      }
    }
  }
}
