import api from '../../plugins/axios'

const textualDescription = {
  state: {
    aboutAzraar: '',
    helpCenter: '',
    howToBuy: '',
    sellerFormURL: '',
    termsAndConditions: ''
  },
  getters: {
    aboutAzraar (state) {
      return state.aboutAzraar
    },
    helpCenter (state) {
      return state.helpCenter
    },
    howToBuy (state) {
      return state.howToBuy
    },
    sellerFormURL (state) {
      return state.sellerFormURL
    },
    termsAndConditions (state) {
      return state.termsAndConditions
    }
  },
  mutations: {
    setAboutAzraar (state, info) {
      state.aboutAzraar = info
    },
    setHelpCenter (state, info) {
      state.helpCenter = info
    },
    setHowToBuy (state, info) {
      state.howToBuy = info
    },
    setSellerFormURL (state, info) {
      state.sellerFormURL = info
    },
    setTermsAndConditions (state, info) {
      state.termsAndConditions = info
    }
  },
  actions: {
    loadTextualDescription (context) {
      api.get('textualDescription')
        .then(response => {
          context.commit('setAboutAzraar', response.data.aboutAzraar)
          context.commit('setHelpCenter', response.data.helpCenter)
          context.commit('setHowToBuy', response.data.howToBuy)
          context.commit('setSellerFormURL', response.data.sellerFormURL)
          context.commit('setTermsAndConditions', response.data.termsAndConditions)
        })
        .catch(() => {})
    }
  }
}

export default textualDescription
