<template>
  <div id="app" style="background-color: #f8f9fa;" ref="app" class="m-0 p-0">
    <app-header :width="windowWidth"></app-header>
    <div style="min-height: 500px">
      <router-view></router-view>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import appHeader from './components/Header.vue'
import appFooter from './components/Footer.vue'
import { mapActions } from 'vuex'

export default {
  data () {
    return {
      windowWidth: 0
    }
  },
  components: {
    appHeader,
    appFooter
  },
  methods: {
    ...mapActions([
      'autoLogin',
      'loadSearchScope',
      'loadTextualDescription'
    ]),
    onResize () {
      if (this.$refs.app) {
        this.windowWidth = this.$refs.app.clientWidth
      }
    }
  },
  created () {
    this.loadSearchScope()
    this.loadTextualDescription()
    this.autoLogin()
  },
  mounted () {
    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize)
    })
    this.onResize()
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.onResize)
  }
}
</script>

