<template>
  <div>
    <b-form @submit="search">
      <b-input-group class="round-right round-left" style="overflow: hidden;">
        <!-- <b-input-group-prepend is-text>
          <b-icon size="lg" icon="search"></b-icon>
        </b-input-group-prepend> -->
        <div>
          <b-form-input
            class="main round-left sizer"
            type="search"
            v-model="searchString"
            :id="'target'+id"
            placeholder=""
            autocomplete="off"
            :style="id === 1 ? {} : { width: barWidth }"
            trim
            @keypress="clip($event, searchString, maxLength)"
          ></b-form-input>
        </div>
        <b-input-group-prepend style="background-color: #227BB1;" class="round-right">
          <b-button type="submit" @click="search" variant="white">
            <b-icon size="lg" variant="white" icon="search" :scale="searchString === '' ? 1.2 : 1.3"
              style="position: relative; left: -2px; color: black"
            ></b-icon>
          </b-button>
        </b-input-group-prepend>
      </b-input-group>
    </b-form>
    <b-popover
      v-if="matchedNames.length"
      custom-class="sizer"
      :show.sync="show"
      :target="'target'+id"
      triggers="focus"
      ref="popover"
      placement="bottom"
    >
      <b-list-group>
        <b-list-group-item class="p-2" v-for="(match, index) in matchedNames" :key="index" :to="getLink(match.item)" @click="show=false;searchString=''">
          <!-- <router-link :to="getLink(match.item)"> -->
          {{match.item.name}}
          <!-- </router-link> -->
      </b-list-group-item>
      </b-list-group>
    </b-popover>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Fuse from 'fuse.js'
import inputClipper from '../mixins/inputClipper'
// import showBrands from './ShowBrands'

export default {
  name: 'SearchBar',
  props: [
    'id',
    'width'
  ],
  data () {
    return {
      searchString: '',
      maxLength: 100,
      show: false
    }
  },
  components: {
    // showBrands
  },
  computed: {
    ...mapGetters([
      'searchProducts',
      'suggestionScope'
    ]),
    barWidth () {
      return (this.width - 80) + 'px'
    },
    matchedNames () {
      if (this.searchString === '') {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.show = false
        // this.$refs.popover.$emit('disable')
        return []
      }
      const fuse = new Fuse(this.suggestionScope, {
        keys: [
          'name',
          'category' // only subCategories will have 'category' property
        ]
      })
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.show = true
      // if (this.show === true) this.$refs.popover.$emit('enable')
      return fuse.search(this.searchString).slice(0, process.env.VUE_APP_SEARCH_BAR_LIMIT)
    }
  },
  mixins: [
    inputClipper
  ],
  methods: {
    getLink (item) {
      if (item.type === 'brand') {
        return {
          name: 'CertainBrand',
          query: {
            name: item.name
          }
        }
      } else if (item.type === 'category') {
        return {
          name: 'CertainCategory',
          query: {
            'cat-name': item.name
          }
        }
      } else if (item.type === 'subCategory') {
        return {
          name: 'CertainSubCategory',
          query: {
            'cat-name': item.category,
            'subcat-name': item.name
          }
        }
      }
    },
    search (event) {
      event.preventDefault()
      if (this.searchString !== '') {
        this.searchString = this.searchString.slice(0, this.maxLength)
        const searchId = parseInt(this.searchString)
        if (this.searchProducts.find(product => product.id === searchId)) {
          this.$router.push({
            name: 'CertainProduct',
            params: {
              id: this.searchString
            }
          }).catch(() => {
          })
        } else {
          this.$router.push({
            name: 'SearchOnAll',
            query: {
              string: this.searchString
            }
          }).catch(() => {
          })
        }
      }
    }
  }
}
</script>

